import { Component, Inject, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { MetaService } from "@ngx-meta/core";
import { NGXLogger } from "../../lib/ngx-logger";
import { LanguageService } from "../../lib/i18n/language/language.service";

@Component({
  selector: "app-verification-screen",
  templateUrl: "./verificationScreen.component.html",
  styleUrls: ["./verificationScreen.component.css"]
})
export class VerificationScreenComponent implements OnInit {
  constructor(
    private route: ActivatedRoute,
    private readonly meta: MetaService,
    private log: NGXLogger,
    private languageService: LanguageService
  ) {}

  ngOnInit() {
    let metaInfo = this.route.snapshot.data.metaLocale[this.languageService.getLocale() || "en-US"];
    // set the meta
    this.meta.setTitle(metaInfo.title);
    this.meta.setTag("description", metaInfo.description);
    this.meta.setTag("keyword", metaInfo.keyword);
    this.meta.setTag("og:image", metaInfo["og:image"]);
    this.meta.setTag("og:locale", this.languageService.getLocale());
    this.meta.setTag("og:locale:alternate", "en-US,fr-FR,ja-JP,th-TH");
  }
}



