import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { VerificationScreenComponent } from "./verificationScreen.component";
import { VerificationFormModule } from "./../verificationForm/verificationForm.module";
import {
  MatButtonModule,
  MatIconModule,
  MatTabsModule,
  MatCardModule,
  MatFormFieldModule,
  MatInputModule,
  MatSelectModule,
  MatProgressBarModule
} from "@angular/material";
import { RouterModule, Routes } from "@angular/router";

@NgModule({
  imports: [
    CommonModule,
    MatButtonModule,
    MatIconModule,
    MatTabsModule,
    MatCardModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatProgressBarModule,
    VerificationFormModule,
    RouterModule.forChild([
      {
        path: "",
        component: VerificationScreenComponent,
        resolve: {},
        data: {
          metaLocale: {
            "en-US": {
              title: "BigToken - Identity Verification",
              description: "BigToken KYC/AML Identity Verification for participation in the BigToken ICO"
            }
          }
        }
      }
    ])
  ],
  declarations: [VerificationScreenComponent],
  providers: []
})
export class VerificationScreenModule {}



